import React from 'react';
import { Theme } from '../../types';
import { Crosshair, Coins, Target } from 'lucide-react';
import { GlobalHelpButton } from '../HelpSystem';

interface MilitaryModuleProps {
  theme: Theme;
  balance: number;
  totalKills?: number;
  isEditingBalance: boolean;
  isEditingKills: boolean;
  tempBalance: string;
  tempKills: string;
  setIsEditingBalance: (editing: boolean) => void;
  setIsEditingKills: (editing: boolean) => void;
  setTempBalance: (balance: string) => void;
  setTempKills: (kills: string) => void;
  handleSaveBalance: () => void;
  handleSaveKills: () => void;
  onHelpClick?: (helpId: string) => void;
}

const MilitaryModule: React.FC<MilitaryModuleProps> = ({
  theme,
  balance,
  totalKills = 0,
  isEditingBalance,
  isEditingKills,
  tempBalance,
  tempKills,
  setIsEditingBalance,
  setIsEditingKills,
  setTempBalance,
  setTempKills,
  handleSaveBalance,
  handleSaveKills,
  onHelpClick
}) => {
  const isDark = theme === 'dark' || theme === 'neomorphic-dark';
  const isNeomorphic = theme.startsWith('neomorphic');
  const cardBg = isNeomorphic
    ? (theme === 'neomorphic-dark' 
      ? 'bg-[#1e1e2e] border-[#1e1e2e] rounded-lg shadow-[8px_8px_16px_rgba(0,0,0,0.4),-8px_-8px_16px_rgba(30,30,46,0.8)] transition-all duration-300' 
      : 'bg-[#e0e5ec] border-[#e0e5ec] rounded-lg shadow-[10px_10px_20px_rgba(163,177,198,0.6),-10px_-10px_20px_rgba(255,255,255,1)] transition-all duration-300')
    : isDark
    ? 'bg-zinc-900 border-zinc-800'
    : 'bg-white border-slate-200';

  return (
    <div className="flex flex-row items-center justify-between gap-2 w-full">
      {/*  */}
      <div className="flex items-center gap-1 flex-shrink-0">
        <span className="text-xs text-zinc-500 uppercase font-bold flex items-center gap-1">
          <Crosshair size={12}/> 
        </span>
      </div>
      
      {/*  */}
      <div className="flex-1"></div>
      
      {/*  */}
      <div className="flex items-center gap-1 flex-shrink-0">
        <Coins size={12} className="text-yellow-500"/>
        <span className="text-zinc-500"></span>
        {isEditingBalance ? (
          <input 
            autoFocus 
            type="number" 
            className={`text-xs font-mono font-bold text-center bg-transparent border-b outline-none w-10 ${isDark ? 'text-yellow-400 border-yellow-600' : 'text-yellow-600 border-yellow-400'}`} 
            value={tempBalance} 
            onChange={(e) => setTempBalance(e.target.value)} 
            onBlur={handleSaveBalance} 
            onKeyDown={(e) => e.key === 'Enter' && handleSaveBalance()}
          />
        ) : (
          <span 
            className={`text-xs font-mono font-black ${isDark ? 'text-yellow-400' : 'text-yellow-600'} cursor-pointer`}
            onDoubleClick={() => setIsEditingBalance(true)}
          >
            {Math.floor(balance)}
          </span>
        )}
      </div>
      
      {/*  */}
      <div className="flex items-center gap-1 flex-shrink-0">
        <Target size={12} className="text-red-500"/>
        <span className="text-zinc-500"></span>
        {isEditingKills ? (
          <input 
            autoFocus 
            type="number" 
            className={`text-xs font-mono font-bold text-center bg-transparent border-b outline-none w-10 ${isDark ? 'text-red-400 border-red-600' : 'text-red-600 border-red-400'}`} 
            value={tempKills} 
            onChange={(e) => setTempKills(e.target.value)} 
            onBlur={handleSaveKills} 
            onKeyDown={(e) => e.key === 'Enter' && handleSaveKills()}
          />
        ) : (
          <span 
            className={`text-xs font-mono font-black ${isDark ? 'text-red-400' : 'text-red-600'} cursor-pointer`}
            onDoubleClick={() => setIsEditingKills(true)}
          >
            {totalKills}
          </span>
        )}
        {/*  */}
        {onHelpClick && (
          <GlobalHelpButton 
            helpId="military" 
            onHelpClick={onHelpClick} 
            size={14} 
            className="hover:scale-[1.1] ml-1" 
          />
        )}
      </div>
    </div>
  );
};

export default MilitaryModule;