// 

export interface Species {
  id: string;
  name: string;
  icon: string;
}

export interface SpeciesData {
  plants: Species[];
  animals: Species[];
}

// 
const baseSpecies = {
  plants: [
    { id: 'pine', name: '', icon: '' },
    { id: 'oak', name: '', icon: '' },
    { id: 'cherry', name: '', icon: '' },
    { id: 'willow', name: '', icon: '' },
    { id: 'bamboo', name: '', icon: '' },
    { id: 'palm', name: '', icon: '' },
    { id: 'cactus', name: '', icon: '' },
    { id: 'mushroom', name: '', icon: '' },
    { id: 'sunflower', name: '', icon: '' },
    { id: 'birch', name: '', icon: '' }
  ],
  animals: [
    { id: 'rabbit', name: '', icon: '' },
    { id: 'fox', name: '', icon: '' },
    { id: 'panda', name: '', icon: '' },
    { id: 'pig', name: '', icon: '' },
    { id: 'chick', name: '', icon: '' },
    { id: 'penguin', name: '', icon: '' },
    { id: 'frog', name: '', icon: '' },
    { id: 'sheep', name: '', icon: '' },
    { id: 'bear', name: '', icon: '' },
    { id: 'bee', name: '', icon: '' }
  ]
};

// UI
export const generateSpeciesWithNumbers = (): SpeciesData => {
  return {
    plants: baseSpecies.plants.flatMap(plant => [
      { ...plant, id: plant.id, name: `${plant.name}1` },
      { ...plant, id: `${plant.id}2`, name: `${plant.name}2` }
    ]),
    animals: baseSpecies.animals.flatMap(animal => {
      if (animal.id === 'bee') {
        // 
        return [
          { ...animal, id: animal.id, name: '' },
          { ...animal, id: `${animal.id}2`, name: '' }
        ];
      } else if (animal.id === 'bear') {
        // 
        return [
          { ...animal, id: animal.id, name: '' },
          { ...animal, id: `${animal.id}2`, name: '' }
        ];
      }
      return [
        { ...animal, id: animal.id, name: `${animal.name}1` },
        { ...animal, id: `${animal.id}2`, name: `${animal.name}2` }
      ];
    })
  };
};

// 3D
export const generateBaseSpecies = (): SpeciesData => {
  return {
    plants: baseSpecies.plants.flatMap(plant => [
      { ...plant, id: plant.id },
      { ...plant, id: `${plant.id}2` }
    ]),
    animals: baseSpecies.animals.flatMap(animal => [
      { ...animal, id: animal.id },
      { ...animal, id: `${animal.id}2` }
    ])
  };
};

// UI
export const SPECIES: SpeciesData = generateSpeciesWithNumbers();

// 3D
export const BASE_SPECIES: SpeciesData = generateBaseSpecies();
